import React, { useContext, useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../contexts/AuthContext';
import '../../styles/header.css'; 

export default function Header() {
    const { isLoggedIn, logout } = useContext(AuthContext);
    const navigate = useNavigate();
    const [shrinkHeader, setShrinkHeader] = useState(false);

    const handleLogout = () => {
        logout();
        navigate('/');
    };

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 50) {
                setShrinkHeader(true);
            } else {
                setShrinkHeader(false);
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <div className={`header ${shrinkHeader ? 'shrink' : ''}`}>
            <div className="logo">
                <Link to="/" className="logo-link">adorable</Link>
                <div className="auth-links">
                    {isLoggedIn ? (
                        <button onClick={handleLogout} className="logout-button"></button>
                    ) : (
                        <Link to="/login"></Link>
                    )}
                </div>
            </div>
            <div className="nav-container">
                <nav className="navigation">
                    <ul className="nav-menu">
                        <li><Link to="/shelter"> </Link></li>
                        <li><Link to="/adoft"> </Link></li>
                        <li><Link to="/"></Link></li>
                    </ul>
                </nav>
                <h1 className="header-title">Adopt a Friend for Life<br /> Find Love in a Shelter Dog<br /> Remember: Every Paw Matter!</h1>
            </div>
        </div>
    );
}
