import React, { useEffect, useState } from 'react';
import axios from 'axios';
import xml2js from 'xml2js';
import { Link, useLocation } from 'react-router-dom';
import '../styles/adoft.css';

function Adoft({ initialCount }) {
    const [animalData, setAnimalData] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const itemsPerPage = initialCount || 12; //     12 
    const pagesToShow = 5; //      
    const location = useLocation();

    useEffect(() => {
        const fetchData = async () => {
            try {
                const serviceKey = 'MrOcEe8%2FwEEFdw6I70lASSq6oPuZeYuYvJkLpfsk6ytKmVmm8V8roL%2BAap6xN9hBTbjat%2Bnmxh0iSQDQjKxn7Q%3D%3D';
                const baseUrl = `https://apis.data.go.kr/6300000/animalDaejeonService/animalDaejeonList?serviceKey=${serviceKey}&searchCondition=1`;
                
                let allData = [];
                let currentPage = 1;
                let totalPages = 1;

                do {
                    const apiUrl = `${baseUrl}&numOfRows=100&pageNo=${currentPage}`;
                    const response = await axios.get(apiUrl, {
                        headers: { 'Content-Type': 'application/xml' },
                        responseType: 'text'
                    });

                    const parser = new xml2js.Parser();
                    const result = await parser.parseStringPromise(response.data);

                    const items = result.ServiceResult.MsgBody[0].items;
                    if (!items) {
                        console.error('No data found');
                        return;
                    }

                    for (let i = items.length - 1; i >= 0; i--) {
                        if (items[i].filePath === undefined) {
                            items.splice(i, 1);
                        }
                    }

                    allData = allData.concat(items.map(item => ({
                        Dog_image: `http://www.daejeon.go.kr/${item.filePath[0]}`,
                        BREEDS: item.species[0],
                        SEX: item.gender[0] === '1' ? '' : item.gender[0] === '2' ? '' : '',
                        FOUND_PLACE: item.foundPlace[0]
                    })));

                    totalPages = parseInt(result.ServiceResult.msgHeader[0].totalPage[0]);
                    currentPage += 1;
                } while (currentPage <= totalPages);

                setAnimalData(allData);
                setTotalPages(totalPages);

            } catch (error) {
                console.error('  :', error);
            }
        };

        fetchData();
    }, []);

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = animalData.slice(indexOfFirstItem, indexOfLastItem);

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    const renderPageNumbers = () => {
        const startPage = Math.max(currentPage - Math.floor(pagesToShow / 2), 1);
        let endPage = Math.min(startPage + pagesToShow - 1, totalPages);
    
        if (endPage - startPage < pagesToShow - 1) {
            // endPage totalPages pagesToShow     startPage 
            const adjustStartPage = Math.max(endPage - (pagesToShow - 1), 1);
            endPage = Math.min(adjustStartPage + pagesToShow - 1, totalPages);
        }
    
        const pageNumbers = [];
    
        //      
        if (currentPage > 1) {
            pageNumbers.push(
                <button key="prev" onClick={() => paginate(currentPage - 1)}>
                    &laquo;
                </button>
            );
        }
    
        //   
        for (let i = startPage; i <= endPage; i++) {
            pageNumbers.push(
                <button
                    key={i}
                    onClick={() => paginate(i)}
                    className={currentPage === i ? 'active' : ''}
                >
                    {i}
                </button>
            );
        }
    
        //      
        if (currentPage < totalPages) {
            pageNumbers.push(
                <button key="next" onClick={() => paginate(currentPage + 1)}>
                    &raquo;
                </button>
            );
        }
    
        return pageNumbers;
    };
    
    

    return (
        <div className="adoft">
            <h2> </h2>
            <div className="animal-list">
                {currentItems.map((dog, index) => (
                    <div key={index} className="animal-card">
                        <img className="animal-image" src={dog.Dog_image || 'placeholder.jpg'} alt={dog.BREEDS} />
                        <h2>: {dog.BREEDS}</h2>
                        <h3>: {dog.SEX}</h3>
                        <h3>: {dog.FOUND_PLACE}</h3>
                    </div>
                ))}
            </div>

            {initialCount === undefined && (
            <div className="pagination">
                {renderPageNumbers()}
            </div>
            )}

            {location.pathname === '/' && (
                <div className="view-all">
                    <Link to="/adoft"></Link>
                </div>
            )}
        </div>
    );
}

export default Adoft;
