import React, { useEffect, useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import Header from './components/layouts/Header';
import Shelter from './components/Shelter';
import Adoft from './components/Adoft';
import Chart from './components/Chart';
import Procedure from './components/Procedure';
import Register from './components/Register';
import Login from './components/Login';
import Logout from './components/Logout';
import { AuthProvider } from '../src/contexts/AuthContext'; // 
import axios from 'axios';
import xml2js from 'xml2js';
import './App.css';

function App() {
    const [breedChartData, setBreedChartData] = useState(null);
    const [sexChartData, setSexChartData] = useState(null);
    const [placeChartData, setPlaceChartData] = useState(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const serviceKey = 'MrOcEe8%2FwEEFdw6I70lASSq6oPuZeYuYvJkLpfsk6ytKmVmm8V8roL%2BAap6xN9hBTbjat%2Bnmxh0iSQDQjKxn7Q%3D%3D';
                const apiUrl = `https://apis.data.go.kr/6300000/animalDaejeonService/animalDaejeonList?serviceKey=${serviceKey}&numOfRows=&pageNo=1&searchCondition=1`;

                const response = await axios.get(apiUrl, {
                    headers: { 'Content-Type': 'application/xml' },
                    responseType: 'text'
                });

                const parser = new xml2js.Parser();
                const result = await parser.parseStringPromise(response.data);

                console.log(result);

                const items = result.ServiceResult.MsgBody[0].items;
                if (!items) {
                    console.error('No data found');
                    return;
                }

                const animalList = items.map(item => ({
                    BREEDS: item.species[0],
                    SEX: item.gender[0] === '1' ? '' : item.gender[0] === '2' ? '' : '',
                    FOUND_PLACE: item.foundPlace[0]
                }));

                const breedCount = {};
                const sexCount = { '': 0, '': 0 };
                const placeCount = {};

                animalList.forEach(animal => {
                    breedCount[animal.BREEDS] = (breedCount[animal.BREEDS] || 0) + 1;
                    sexCount[animal.SEX] += 1;
                    placeCount[animal.FOUND_PLACE] = (placeCount[animal.FOUND_PLACE] || 0) + 1;
                });


            } catch (error) {
                console.error('  :', error);
            }
        };

        fetchData();
    }, []);

    return (
        <AuthProvider>
            <div className="App">
                <Header />
                <Routes>
                    <Route path="/" element={<AppContent />} />
                    <Route path="/charts" element={<Chart />} />
                    <Route path="/adoft" element={<Adoft />} />
                    <Route path="/shelter" element={<Shelter />} />
                    <Route path="/procedure" element={<Procedure />} />
                    <Route path="/register" element={<Register />} />
                    <Route path="/login" element={<Login />} />
                    <Route path="/logout" element={<Logout />} />
                    <Route path="/register" element={<Register />} />
                </Routes>
            </div>
        </AuthProvider>
    );
}

function AppContent() {
    return (
        <div className="AppContent">
            <Adoft initialCount={4} />
            <Chart />
            <Procedure />
        </div>
    );
}

export default App;
