import React, { useEffect, useState } from 'react';
import '../styles/chart.css';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import axios from 'axios';
import xml2js from 'xml2js';

// Chart.js  
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

function Chart() {
    const [chartData, setChartData] = useState({
        labels: [],
        datasets: [
            {
                label: '',
                data: [],
                backgroundColor: '',
                borderColor: '',
                borderWidth: 1,
            },
        ],
    });

    useEffect(() => {
        const fetchData = async () => {
            const serviceKey = 'MrOcEe8%2FwEEFdw6I70lASSq6oPuZeYuYvJkLpfsk6ytKmVmm8V8roL%2BAap6xN9hBTbjat%2Bnmxh0iSQDQjKxn7Q%3D%3D'; //    
            const url = `https://apis.data.go.kr/6300000/animalDaejeonService/animalDaejeonList?serviceKey=${serviceKey}&numOfRows=10&pageNo=1&searchCondition=3`;
            
            try {
                const response = await axios.get(url, {
                    headers: { 'Content-Type': 'application/xml' },
                    responseType: 'text'
                });
                console.log(response.data); //   

                const parser = new xml2js.Parser();
                const result = await parser.parseStringPromise(response.data);
                console.log(result); //   

                const items = result.ServiceResult.MsgBody[0].items;
                console.log(items); // items 
                if (!items) {
                    console.error('No data found');
                    return;
                }

                const adoptionStatusData = {
                    labels: [
                        '', '', '', '', '',
                        '', '', '', '', '',
                        '', ''
                    ],
                    datasets: [
                        {
                            label: '   ',
                            data: Array(12).fill(0),
                            backgroundColor: 'rgba(75,192,192,0.4)',
                            borderColor: 'rgba(75,192,192,1)',
                            borderWidth: 1
                        }
                    ]
                };

                items.forEach(item => {
                    const statusIndex = parseInt(item.adoptionStatusCd[0], 10) - 1;
                    console.log(statusIndex); //    
                    if (statusIndex >= 0 && statusIndex < 12) {
                        adoptionStatusData.datasets[0].data[statusIndex]++;
                    }
                });

                setChartData(adoptionStatusData);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, []);

    return (
        <div className="charts">
            <Bar
                data={chartData}
                options={{
                    scales: {
                        y: {
                            beginAtZero: true
                        }
                    }
                }}
            />
        </div>
    );
};

export default Chart;
